import axios from 'axios';
import configs from '../../configs';

axios.defaults.baseURL = configs.baseURL;

// GET

export const _getAllArtforms = () => axios.get('/artform/all');

export const _getAllOrganizations = (params) => axios.get('/organization/all', {params});

export const _getOneOrganization = (id) => axios.get(`/organization/get?id=${id}`);

export const _getAllArtists = (params) => axios.get('/artist/all', {params});

export const _getOneArtist = (id) => axios.get(`/artist/get?id=${id}`);



// POST  

export const _getPresignedUrl = (data) => axios.post('/file/upload', data);

export const _postArtforms = (data) => axios.post('/artform/add', data);

export const _postOrganization = (data) => axios.post('/organization/add', data);

export const _postArtist = (data) => axios.post('/artist/add', data);


// PUT 

export const _uploadFileAws = (url, file, type) => (
    axios.put(url, file, { headers: {
        'Content-Type': type
    } })
) 


// PATCH

export const _updateArtform = (id, data) => axios.patch(`/artform/update?id=${id}`, data);

export const _updateOrganization = (id, data) => axios.patch(`/organization/update?id=${id}`, data);

export const _updateArtist = (id, data) => axios.patch(`/artist/update?id=${id}`, data);




// DELETE 

export const _deleteArtform = (id) => axios.delete(`/artform/delete?id=${id}`);

export const _deleteOrganization = (id) => axios.delete(`/organization/delete?id=${id}`);

export const _deActivateArtist = (id) => axios.delete(`/artist/deactivate?id=${id}`);






