import React, { Component } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import { motion } from "framer-motion";

import ArtistCard from '../../components/ArtistCard';
import BottomSlider from '../../components/uiElements/BottomSlider';
import Dropdown from '../../components/uiElements/Dropdown';
import { animateConfigs, colors } from '../../constants';
import { SearchIcon, LocationIcon, SortIcon, ArtformIcon, SwitchIcon, Wallet, ChevronDown } from '../../components/svgComponents';
import classes from '../artists/artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

const OPTIONS = ['Mixed', 'Warli', 'Tanjore', 'Folk Art', 'Mandala'];

const DUMMY_NFTS = [
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
];

export default class Nft extends Component {

    constructor(props) {
        super(props);
        this.state = {
            activeTab:'Artists'
        }; 
    }

    renderArtists = () => {
        return DUMMY_NFTS.map((art, index) => {
            return <Col lg={3}>
                <ArtistCard
                    key={index}
                    image={art.image}
                    name={art.name}
                    date={art.date}
                    status={'Sold'}
                    availableNo={null}
                    bottomLeftTitle={
                        <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                            <div style={{marginTop:-3}}><Wallet size={15} color={colors.inActiveIcon}/></div>
                            <div style={{opacity:0.8, marginLeft:2}}>{'12.05 N'}</div>
                        </div>}
                    onClick={() => this.props.history.push('/viewartistnft/1235678')}
                />
        </Col>
        })
    }

    render() {

        const { activeTab } = this.state;

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}>
                    <div style={{fontWeight:600, fontSize:22}}>
                        Manage NFTs (25)
                    </div>
                    <div>
                        <Button onClick={() => this.props.history.push('/createartist')} style={{width:125, height:40, marginLeft:10}} className={globalClasses.primaryBtn}>New NFT</Button>
                    </div>
                </div>
                <div style={{marginTop:40}}>
                    <Row style={{marginBottom:30}}>
                        <Col lg={3}>
                            <Dropdown 
                                title="Location" 
                                value="All"
                                icon={<LocationIcon size={20} color="#99A3B7"/>}
                                options={OPTIONS}
                            />  
                        </Col>
                        <Col lg={3}>
                            <Dropdown 
                                title="Status" 
                                value="All"
                                icon={<SwitchIcon size={20} color="#99A3B7"/>}
                                options={OPTIONS}
                            />    
                        </Col>
                        <Col lg={3}>
                            <Dropdown 
                                title="Artform" 
                                value="All"
                                icon={<ArtformIcon size={20} color="#99A3B7"/>}
                                options={OPTIONS}
                            />    
                        </Col>
                        <Col lg={3}>
                            <Dropdown 
                                title="Sort By" 
                                value="All"
                                icon={<SortIcon size={20} color="#99A3B7"/>}
                                options={OPTIONS}
                            />    
                        </Col>
                    </Row>
                </div>
                <div>
                    <Row>
                        {this.renderArtists()}            
                    </Row>
                </div>
            </motion.div>
        )
    }
}
