import React from 'react';
import { Row, Col, Button } from 'react-bootstrap';
import { motion } from "framer-motion";

import ArtistCard from '../../components/ArtistCard';
import { animateConfigs } from '../../constants';
import { colors } from '../../constants';
import { ChevronLeft, Instagram, Facebook, Globe, Wallet } from '../../components/svgComponents';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';
import Badge from '../../components/uiElements/Badge';

const DUMMY_ARTS = [
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
];


export default function ViewArtistNft({ history }) {

    const renderArtwork = () => {
        return DUMMY_ARTS.map((art, index) => {
            return <Col lg={6}>
                <ArtistCard
                    key={index}
                    image={art.image}
                    name={art.name}
                    date={art.date}
                    status={'Sold'}
                    availableNo={null}
                    bottomLeftTitle={
                        <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                            <div style={{marginTop:-3}}><Wallet size={15} color={colors.inActiveIcon}/></div>
                            <div style={{opacity:0.8, marginLeft:2}}>{'12.05 N'}</div>
                        </div>}
                    onClick={() => history.push('/viewartistnft/1235678')}
                />
            </Col>
        }) 
    } 

    const emptyState = () => {
        return <div style={{textAlign:'center', marginTop:'25%'}}>
            <div style={{fontSize:26, opacity:0.7}}>No NFTs added by this artist</div>
            <Button onClick={() => history.push('/createartistnft')} className={globalClasses.outlinePrimaryBtn} style={{width:100, marginTop:10}}>
                New NFT
            </Button>
        </div>
    }
    
    return (
        <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
            <header className={globalClasses.flexRowSpace} style={{marginBottom: '40px'}}>
                <div onClick={() => history.goBack()} className={globalClasses.flexRow} style={{cursor:'pointer'}}>
                    <ChevronLeft size={16}/>
                    <span style={{fontWeight:600, fontSize:22, marginLeft:17, marginTop:-2}}>NoBrainer</span>
                </div>
                <div>
                    <Button variant="outline-dark"  style={{width:125}}>Archive NFT</Button>
                    <Button variant="primary" className={globalClasses.outlinePrimaryBtn} style={{marginLeft:10, height:42}}>List in marketplace</Button>
                </div>
            </header>
            <Row>
                <Col lg={5}>
                    <img style={{width:'100%', borderRadius:7}} src={'https://images.unsplash.com/photo-1565073182887-6bcefbe225b1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8NXx8fGVufDB8fHx8&w=1000&q=80'} alt="profile"/>
                </Col>
                <Col lg={7} style={{fontSize:18}}>
                    <Badge style={{width:90, fontSize:16}}>
                        Unlisted
                    </Badge>
                    <div style={{fontWeight:600, fontSize:40, marginTop:5}}>NoBrainer</div>
                    <div style={{fontWeight:600, fontSize:20, color: colors.nakshBlue}}>Artist Name</div>
                    <div style={{background:'#E6EEFF', borderRadius:7, padding:'10px 15px', marginTop:15, marginBottom:15}}>
                        <div style={{marginBottom:15}}>
                            <span style={{fontWeight:600, fontSize:28}}>0 N</span> 
                            <span style={{fontSize:24, marginLeft:10}}>/ item</span>
                        </div>
                        <div>Royalties: 0%</div>
                    </div>
                    <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Quantity</div>
                        <div>5</div>
                    </div>
                    <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Description</div>
                        <div>Aliqua id fugiat nostrud irure ex duis ea quis id quis ad et. Sunt qui esse pariatur duis deserunt mollit dolore cillum minim tempor enim. Elit aute irure tempor cupidatat incididunt sint deserunt ut voluptate aute id deserunt nisi.</div>
                    </div>
                    <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Material</div>
                        <div>Canvas</div>
                    </div>
                    <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Collection</div>
                        <div>Collection Name</div>
                    </div>
                    <div style={{marginBottom:25}}>
                        <div style={{fontWeight:600}}>Custom field</div>
                        <div>Aliqua id fugiat nostrud irure ex duis ea quis id quis ad et. Sunt qui esse pariatur duis deserunt mollit dolore cillum minim tempor enim. </div>
                    </div>
                    <Button variant="outline-danger" style={{width:'100%', border:'1px solid #E62F2F'}}>Burn NFT</Button>
                </Col>
            </Row>
        </motion.div>
    )
}

