import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import Artists from '../pages/artists/Artists';
import CreateArtist from '../pages/artists/CreateArtist';
import CreateArtistNft from '../pages/artists/CreateArtistNft';
import ViewArtist from '../pages/artists/ViewArtist';
import ViewArtistNft from '../pages/artists/ViewArtistNft';
import Artforms from '../pages/artforms/Artforms';
import Nft from '../pages/nft/Nft';
import Wallet from '../pages/wallet/Wallet';
import NotFound from '../pages/NotFound';
import CreateOrganization from '../pages/artists/CreateOrganization';
import ViewOrganization from '../pages/artists/ViewOrganization';

export default function Routes() {
    return (
        <Switch>
            <Route path="/" component={Artists} exact/>
            <Route path="/organization" component={Artists}/>
            <Route path="/createartist" component={CreateArtist}/>
            <Route path="/createartistnft" component={CreateArtistNft}/>
            <Route path="/createorganization" component={CreateOrganization}/>
            <Route path="/viewartist/:id" component={ViewArtist}/>
            <Route path="/viewartistnft/:id" component={ViewArtistNft}/>
            <Route path="/vieworganization/:id" component={ViewOrganization}/>
            <Route path="/editartist" component={CreateArtist}/>
            <Route path="/editorganization" component={CreateOrganization}/>
            <Route path="/artforms" component={Artforms}/>
            <Route path="/nfts" component={Nft}/>
            <Route path="/connectwallet" component={Wallet}/>
            <Route path='/404' component={NotFound}/>
            <Redirect from='*' to='/404' exact/>
        </Switch>
    )
}

