import React from 'react';
import PropTypes from 'prop-types';

import { ChevronDown } from '../../components/svgComponents';
import classes from '../../constants/globalStyles.module.css';

export default function Dropdown({ icon, title, options, value }) {

    const [show, setShow] = React.useState(false);

    return (
        <div>
            <div onClick={() => setShow(!show)} className={classes.dropdown}>
                <div style={{display:'flex', alignItems:'center'}}>
                    {icon}
                    <div style={{color:'#99A3B7', marginRight:5, marginLeft:8, fontSize:17}}>{title}:</div>
                    <div>{value}</div>
                </div>
                <ChevronDown/>
            </div>
            {show && 
            <div style={{marginTop:10, zIndex:5, display:'block', padding:'1px 15px', position:'absolute', width:'19.2%'}} className={classes.dropdown}>
                {options.map((option, i) => {
                    return <div key={i} style={{margin:'15px 0px'}}>{option}</div>
                })}
            </div>}
        </div>
    )
}

Dropdown.propTypes = {
    value: PropTypes.string,
    title: PropTypes.string,
    onClick: PropTypes.func
}; 
