import React from 'react';
import { NavLink, useLocation } from 'react-router-dom';

import { Image, AppLogo, Users, Wallet, ExternalLink, SidebarArtform } from '../svgComponents';
import logo from '../../assets/images/Dlogo.svg';
import { colors } from '../../constants';
import classes from './sidebar.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

function Sidebar(props) {

    const location = useLocation();

    React.useEffect(() => {
        changeActiveState();
    },[location.pathname])

    const [changeIconColor, setChangeIconColor] = React.useState({
        icon1: colors.inActiveIcon,
        icon2: colors.inActiveIcon,
        icon3: colors.inActiveIcon,
        icon4: colors.inActiveIcon
    });

    function changeActiveState() {
        if(location.pathname === "/" || location.pathname === "/createartist") {
            handleMouseOver('icon1');
            changeInactive('icon2');
            changeInactive('icon3');
            changeInactive('icon4');
        } else if(location.pathname === "/nfts" || location.pathname === "/viewartistnft/:id") {
            handleMouseOver('icon2');
            changeInactive('icon1');
            changeInactive('icon3');
            changeInactive('icon4');
        } else if(location.pathname === "/artforms"){
            handleMouseOver('icon3');
            changeInactive('icon1');
            changeInactive('icon2');
            changeInactive('icon4');
        } else if(location.pathname === "/connectwallet"){
            handleMouseOver('icon4');
            changeInactive('icon1');
            changeInactive('icon2');
            changeInactive('icon3');
        }
    }

    function changeInactive(icon) {
        setChangeIconColor(prevState => ({
            ...prevState,
            [icon]: colors.inActiveIcon
        }));
    }

    function handleMouseOver(icon) {
        setChangeIconColor(prevState => ({
            ...prevState,
            [icon]: colors.activeIcon
        }));
    }

    function handleMouseLeave(icon) {
        changeInactive(icon);
        changeActiveState();
    }


    return (
        <div className={classes.sidebarContainer}>
            <div className={classes.appLogoContainer}> 
                {/* <AppLogo size={30} color={'#3751FF'}/> */}
                <img src={logo} alt={'logo'}/>
                {/* <div style={{fontSize: '21px', color: '#A4A6B3', fontWeight: 600}}>
                    Naksh
                </div> */}
            </div>
            <NavLink exact={location.pathname === "/nfts" || location.pathname === "/artforms" || location.pathname === "/viewartistnft/1235678" || location.pathname === "/connectwallet" || location.pathname === "/404" ? true : false} to="/" className={classes.appBarItems} activeClassName={classes.activeAppBarItems} onMouseOver={() => handleMouseOver('icon1')} onMouseLeave={() => handleMouseLeave('icon1')}>
                <Users size={17} color={changeIconColor.icon1}/>
                <div style={{marginLeft:20}}>Artists</div>
            </NavLink>
            <NavLink to="/nfts" className={classes.appBarItems} activeClassName={classes.activeAppBarItems} onMouseOver={() => handleMouseOver('icon2')} onMouseLeave={() => handleMouseLeave('icon2')}>
                <Image size={17} color={changeIconColor.icon2}/>
                <div style={{marginLeft:20}}>NFTs</div>
            </NavLink>
            <NavLink to="/artforms" className={classes.appBarItems} activeClassName={classes.activeAppBarItems} onMouseOver={() => handleMouseOver('icon3')} onMouseLeave={() => handleMouseLeave('icon3')}>
                <SidebarArtform size={17} color={changeIconColor.icon3}/>
                <div style={{marginLeft:20}}>ArtForms</div>
            </NavLink>
            <div style={{margin:'10px 0px'}} className={globalClasses.underline}/>
            <NavLink to="/connectwallet" className={classes.appBarItems} activeClassName={classes.activeAppBarItems} onMouseOver={() => handleMouseOver('icon4')} onMouseLeave={() => handleMouseLeave('icon4')}>
                <div style={{marginRight:5, fontSize:16}}>Open Marketplace</div>
                <ExternalLink size={16} color={colors.inActiveIcon}/>
            </NavLink>
            <div className={classes.appBarItems} onMouseOver={() => handleMouseOver('icon4')} onMouseLeave={() => handleMouseLeave('icon4')}>
                <div style={{fontSize:16}}>Logout</div>
            </div>
        </div>
    )
}

export default Sidebar;
